

body {
  line-height: 1.6;
  font-family: 'Kavivanar', cursive, fantasy, sans-serif;
}
pre {
    font-family: "Inconsolata", courier, monospace; /* ot "Curtive Mono", "Inconsolata", "Nova Mono" */
    overflow: auto;
    height: 45rem;
    width: 100%;
    scroll-behavior: auto;
    height: 600px;
    overflow: none;
}
pre input {
  background-color: #222;
  color: #dbd1b4;
  border-collapse: separate;
  border: 3px solid #eff;
  padding: 0.3rem;
}
.dark {
  background-color: #222;
  color: #dbd1b4;
}
.light {
  background-color: #dbd1b4;
  color: #222;
}
.dark input, .dark select, .dark button {
  background-color: #222;
  color: #dbd1b4;
  border: thin solid #dbd1b4;
  -webkit-box-shadow: 5px 5px 5px #dbd1b4;
          box-shadow: 5px 5px 5px #dbd1b4;

}
.light input, .light select, .light button {
  background-color: #dbd1b4;
  color: #222;
  border: thin solid #222;
  -webkit-box-shadow: 5px 5px 5px #222;
          box-shadow: 5px 5px 5px #222;
}
.hammerContainer span { -webkit-user-select: text !important; -moz-user-select: text !important; -ms-user-select: text !important; user-select: text !important; }
.input-group {
  width: 100%;
}
.form-input {
  border: 2px solid #eee;
  width: 100% !important;
}
@media only screen and (max-width : 530px)
{
  pre.responsive-font-size{
    font-size: 10pt;
    white-space: pre-wrap;
  }

}
@media only screen and (min-width : 531px)
{
  pre.responsive-font-size{
    font-size: 11pt;
    white-space: pre-wrap;
  }
}
@media only screen and (min-width : 670px)
{
  pre.responsive-font-size{
    font-size: 12pt
  }
}
@media only screen and (min-width : 870px)
{
  pre.responsive-font-size{
    font-size: 14pt
  }
}
pre select, pre button {
  background-color: #222;
  color: #fef;
  border-collapse: collapse;
  border: 1px solid #eff;
  border-radius: 2rem;
  padding: 1rem;
  margin: 1rem;
}
::-webkit-input-placeholder {
    color:    #AAA;
}
:-ms-input-placeholder {
    color:    #AAA;
}
::-ms-input-placeholder {
    color:    #AAA;
}
::placeholder {
    color:    #AAA;
}
.mod-string-list {
  font-family: Verdana, Arial, sans-serif;
  font-size: 11pt;
  padding: 1em;
  border: 2px solid #ccc;
/*  background-color: #eee; */
  color: #333;
  height: auto;
  max-height: 600px;
  scroll-behavior: auto;
}
.mod-string-list {
  margin: 0;
}
.mod-string-list label {
  width: 100%;
  display: block;
  height: 100%;
  padding-left: 1em;
}

  .mod-string-list input:not(.accept), .mod-string-list select, .mod-string-list button {
      max-width: 150px;
      width: 150px;
      min-width: 20px;

        display: inline-block;
        float: left;
  }

  .mod-string-list input[type="checkbox"] {
        display: inline-block;
        margin-top:13px;
        margin-left: 8px;
  }
  .mod-string-list > div {
    width: 123px;
    max-width: 123px;
    min-width: 123px;
  }
  .loader{
    text-align: center;
    vertical-align: middle;
    font-family: "Kavivanar", cursive;
    font-size: 64pt;
    display: block;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border: 1px solid #93D;
    background-color: #DDD;
    z-index: 4;
  }
  .highlight {
      background-color: #9f9;
  }
  .form-inputs {
      display: block;
      position: absolute;
      text-align: center;
      top: 200px;
      left: 300px;
      width: 140px;
      height: 100px;
      background-color: #FFFFEE;
      border: 2px solid #FCC;
  }
  .x {
    width: 20px;
    font-weight: bolder;
    color: #E19;
  }

  .x:hover {
      background-color: #FFF;
      color: #000;
      cursor: pointer;
  }
  .mod-string-list .x {
    position: relative;
    left: 150px;
    text-align: center;
    top: -51px;
  }
  .ihover {
    background-color: inherit;
  }
  .ihover:hover {
    background-color: #9f9;
  }
  .slug {
      font-size: 10px;
      font-style: italic;
  }
  h3 {
      font-size: 14pt;
      margin-bottom: 0.3em;
  }
  .delete {
      display: none;
  }



